// 代表在 client 瀏覽器端執行，沒寫就會是在 server 端執行
'use client';

import { useState } from 'react';
// use 開頭都是勾子 hooks，勾子只能在客戶端使用
// useState 讓函式型元件可以使用和管理狀態 (資料)
export interface PageProps {}

// 看起來是函式，但在 React 裡它是元件
// 不是所有的勾子的語法都相同，React 有 6 大類勾子
// export default 唯一的預設導出，所以一個程式碼檔案、擁有一個元件
export default function CounterPage({}: PageProps) {
  // 前面是狀態，後面要更動前面的狀態
  const [count, setCount] = useState<number>(99);

  return (
    // 以下是 JSX 的世界，只有在 React 裡面能用
    // 渲染的東西是元件的回傳值
    <>
      {/* 挖一個老鼠洞把值放進來 */}
      <h1>{count}</h1>
      <button
       onClick={()=>{
        setCount(count + 1);
       }}
      >+1</button>
    </>
  );
}
